<template>
  <div class="topics-item">
    <div class="cell">
      <router-link :to="`user/${topic.author.loginname}`">
        <el-avatar
          shape="square"
          :size="30"
          :src="topic.author.avatar_url"
        ></el-avatar
      ></router-link>
      <span class="reply_visit"
        ><span class="reply_count">{{ topic.reply_count }}</span
        ><span class="count_seperator">/</span
        ><span class="visit_count">{{ topic.visit_count }}</span></span
      >
      <span v-if="isShowTag" :class="tagStyle">{{
        topic | getTopicTagText
      }}</span>
      <h4>
        <router-link
          class="topic_title"
          :to="{ name: 'topic', params: { id: topic.id } }"
          >{{ topic.title }}</router-link
        >
      </h4>
      <span class="last_active_time">{{
        topic.last_reply_at | moment("from")
      }}</span>
    </div>
  </div>
</template>

<script>
export default {
  props: ["topic"],
  computed: {
    isShowTag() {
      const { path } = this.$route;
      return (
        this.topic.top || path === "/" || path === "/all" || path === "/good"
      );
    },
    tagStyle() {
      const { top, good } = this.topic;
      return top || good ? "put_top " : "topiclist-tab";
    },
  },
};
</script>

<style lang="less" scoped>
.topics-item {
  .cell {
    background: #fff;
    border-top: 1px solid #f0f0f0;
    padding: 10px;
    font-size: 14px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .reply_visit {
      width: 80px;
      text-align: center;
      flex-shrink: 0;
      .reply_count {
        color: #9e78c0;
        font-size: 14px;
      }
      .visit_count {
        font-size: 10px;
        color: #b4b4b4;
      }
    }
    .topiclist-tab {
      background-color: #e5e5e5;
      color: #999;
      padding: 2px 4px;
      border-radius: 3px;
      font-size: 12px;
      flex-shrink: 0;
    }
    .put_top {
      background: #80bd01;
      padding: 2px 4px;
      border-radius: 3px;
      color: #fff;
      font-size: 12px;
      flex-shrink: 0;
    }
    h4 {
      flex-grow: 1;
      margin-left: 10px;
      font-size: 16px;
      margin-right: 20px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      a.topic_title {
        color: #333;
        text-decoration: none;
      }
      a.topic_title:hover {
        text-decoration: underline;
      }
      a.topic_title:visited {
        color: #888;
      }
    }
    .last_active_time {
      color: #778087;
      flex-shrink: 0;
    }
  }
  .cell:hover {
    background: #f5f5f5;
  }
}
.topics-item:last-child {
  border-bottom: 1px solid #ccc;
}
</style>