<template>
  <div v-if="topic" class="topic">
    <PanelContainer>
      <template #content>
        <div class="topic-content">
          <h2>{{ topic.title }}</h2>
          <div class="topic-head">
            <div class="topic-info">
              <span>发布于 {{ topic.create_at | moment("from", "now") }}</span>
              <span>作者 {{ topic.author.loginname }}</span>
              <span>{{ topic.visit_count }} 次浏览</span>
              <span>来自 {{ topic | getTopicTagText }}</span>
            </div>
            <template v-if="userInfo">
              <el-button
                @click="collect"
                v-if="!topic.is_collect"
                type="success"
                size="mini"
                >收藏</el-button
              >
              <el-button
                @click="deCollect"
                v-if="topic.is_collect"
                type="default"
                size="mini"
                >取消收藏</el-button
              ></template
            >
          </div>
          <hr />
          <div class="topic-html" v-html="topic.content"></div>
        </div>
      </template>
    </PanelContainer>
    <!-- 评论列表 -->
    <PanelContainer>
      <template #head>{{ topic.reply_count }}回复</template>
      <template #content>
        <RepliesItem
          v-for="(reply, index) in topic.replies"
          :key="reply.id"
          :reply="reply"
          :index="index"
          :mostReplyUpsId="mostReplyUpsId"
          :userInfo="userInfo"
          @getTopic="getTopic"
        />
      </template>
    </PanelContainer>
    <!-- 评论框 -->
    <PanelContainer v-if="userInfo">
      <template #head>添加回复</template>
      <template #content>
        <!-- 输入评论 -->
        <el-input type="textarea" v-model.trim="replyText"></el-input>
        <el-button @click="addReply">提交</el-button>
      </template>
    </PanelContainer>
  </div>
</template>

<script>
import RepliesItem from "../components/RepliesItem.vue";
import PanelContainer from "../components/PanelContainer.vue";
import {
  collectTopic,
  getTopic,
  deCollectTopic,
  addTopicReply,
} from "../http/api";
import { mapState } from "vuex";
export default {
  components: {
    PanelContainer,
    RepliesItem,
  },
  props: ["id"],
  data() {
    return {
      topic: null,
      replyText: "",
    };
  },
  computed: {
    mostReplyUpsId() {
      let id = "",
        num = 0;
      this.topic.replies.forEach((reply) => {
        if (reply.ups.length > num) {
          id = reply.id;
          num = reply.ups.length;
        }
      });
      return id;
    },
    ...mapState(["userInfo"]),
    accesstoken() {
      const accesstoken = localStorage.getItem("accesstoken");
      return accesstoken || "";
    },
  },
  async created() {
    //  let params = { id: this.id };
    //   if (this.accesstoken) {
    //     params.accesstoken = this.accesstoken;
    //   }
    //   const res = await getTopic(params);
    //   this.topic = res.data;
    this.getTopic();
  },
  methods: {
    async collect() {
      const { topic, accesstoken } = this;
      await collectTopic({ topic_id: topic.id, accesstoken });
      this.topic.is_collect = true;
    },
    async deCollect() {
      const { topic, accesstoken } = this;
      await deCollectTopic({ topic_id: topic.id, accesstoken });
      this.topic.is_collect = false;
    },
    async getTopic() {
      let params = { id: this.id };
      if (this.accesstoken) {
        params.accesstoken = this.accesstoken;
      }
      const res = await getTopic(params);
      this.topic = res.data;
    },
    async addReply() {
      let { replyText } = this;
      if (replyText) {
        const res = await addTopicReply({
          accesstoken: this.accesstoken,
          content: replyText,
          id: this.topic.id,
        });
        replyText = "";
        console.log(res);
        this.getTopic();
      }
    },
  },
};
</script>

<style lang="less" scoped>
.topic-content {
  padding: 20px;
  .topic-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 20px;
    .topic-info span {
      font-size: 12px;
      color: #838383;
      margin-right: 10px;
    }
  }
  /deep/ .topic-html {
    padding-top: 20px;
    img {
      width: 100%;
    }
  }
}
</style>