<template>
  <div class="user-detail">
    <PanelContainer>
      <template #head><router-link to="/">主页</router-link>/</template>
      <template #content>
        <div>
          <el-avatar
            shape="square"
            :size="30"
            :src="userDetail.avatar_url"
          ></el-avatar>
          <span>{{ userDetail.loginname }}</span>
        </div>
        <span>{{ userDetail.score }}积分</span>
      </template>
    </PanelContainer>
    <PanelContainer>
      <template #head><span>最近创建的话题</span></template>
      <template #content
        ><TopicsItem
          v-for="item in userDetail.recent_topics"
          :key="item.id"
          :topic="item"
      /></template>
    </PanelContainer>
    <PanelContainer>
      <template #head><span>最近参与的话题</span></template>
      <template #content
        ><TopicsItem
          v-for="item in userDetail.recent_replies"
          :key="item.id"
          :topic="item"
      /></template>
    </PanelContainer>
  </div>
</template>

<script>
import { getUserDetail } from "../http/api";
import PanelContainer from "../components/PanelContainer.vue";
import TopicsItem from "../components/TopicsItem.vue";
export default {
  components: {
    PanelContainer,
    TopicsItem,
  },
  props: ["loginname"],
  watch: {
    loginname: {
      async handler(value) {
        // console.log(value);
        const res = await getUserDetail({ loginname: value });
        this.userDetail = res.data;
      },
      immediate: true,
    },
  },
  data() {
    return {
      userDetail: null,
    };
  },
};
</script>

