<template>
  <div :class="['reply', { active: mostReplyUpsId === reply.id }]">
    <div class="author_content">
      <div class="left">
        <router-link :to="`/user/${reply.author.loginname}`">
          <el-avatar
            shape="square"
            :size="30"
            :src="reply.author.avatar_url"
          ></el-avatar
        ></router-link>
        <span class="user_info"
          ><a href="#" class="reply_author">{{ reply.author.loginname }}</a>
          <a href="#" class="reply_time"
            >{{ index + 1 }}楼 ·{{ reply.create_at | moment("from") }}</a
          ></span
        >
      </div>
      <div class="operation" v-if="reply.ups.length">
        <span
          class="ups"
          @click="handleLike"
          :class="{ is_uped: reply.is_uped }"
          >赞{{ reply.ups.length }}</span
        >
        <span v-if="userInfo">回</span>
      </div>
    </div>
    <div class="text" v-html="reply.content"></div>
  </div>
</template>

<script>
import { replyLike } from "../http/api";
export default {
  props: ["reply", "index", "mostReplyUpsId", "userInfo"],
  methods: {
    async handleLike() {
      const accesstoken = localStorage.getItem("accesstoken");
      if (accesstoken) {
        const res = await replyLike({ reply_id: this.reply.id, accesstoken });
        console.log(res);
        this.$emit("getTopic");
      } else {
        this.$message({
          message: "请登录",
        });
      }
    },
  },
};
</script>

<style lang="less" scoped>
.reply {
  &.active {
    background-color: #f0f0fcd6;
  }
  display: flex;
  flex-direction: column;
  padding: 10px;
  .author_content {
    display: flex;
    justify-content: space-between;
    .left {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      .user_info {
        margin-left: 10px;
        .reply_author {
          color: #666;
          font-size: 14px;
          font-weight: bold;
        }
        .reply_time {
          color: #08c;
          font-size: 12px;
        }
      }
    }
    .operation {
      margin-right: 20px;
      .ups {
        cursor: pointer;
      }
      .is_uped {
        color: red;
      }
    }
  }
  .text {
    margin-left: 50px;
    margin-bottom: 1em;
    width: 80%;
  }
}
</style>